@import "./../../styles/variables.scss";

.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.681);
    z-index: 1;
}

.ModalContainer {
    background-color: black;
    z-index: 2;
    position: fixed;
    width: 90vw;
    max-width: 700px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 1.5rem 4rem rgba($black, 0.15);
    transition: all 0.3s;
    border-radius: 10px;

    &__Logo {
        max-width: 100px;
        margin-bottom: 10px;
    }


    &__Poster {
        border-radius: 10px;
        width: 100%;
        height: 300px;
        object-fit: cover;
        object-position: top;
        z-index: -10;
    }

    .ModalData {
        padding: 20px;
        background-color: black;
        z-index: 15;

        &__Title {
            margin-top: 5px;
            font-size: 25px;
            margin-bottom: 3px;

        }

        &__BtnCir {
            cursor: pointer;
            margin-top: 15px;
            margin-right: 7px;
            font-size: 50px;
        }

        &__BtnCir:hover {
            color: rgb(173, 173, 173);
        }

        &__Season {
            font-size: 15px;
            font-weight: 300;
            margin-bottom: 5px;
            border: 1px solid;
            max-width: 80px;
            text-align: center;
            border-radius: 5px;

        }

        &__Desc {
            font-weight: 400;
            font-size: 13px;

        }

        // &__Genres {
        //     font-size: 15px;
        //     font-weight: 300;
        //     margin-top: 5px;
        //     border: 1px solid;
        //     max-width: 300px;
        //     text-align: center;
        //     border-radius: 5px;
        // }

        &__Cast {
            font-weight: 400;
            font-size: 13px;
            margin-top: 5px;
            color: rgb(109, 109, 109);

        }
    }

    &__BtnClose {
        cursor: pointer;
        font-size: 40px;
        float: right;
        position: absolute;
        top: 10px;
        right: 10px;


    }

    &__BtnClose:hover {
        color: rgb(173, 173, 173);
    }



    @media all and (min-width: 600px) {

        .ModalData {


            &__Title {
                font-size: 40px;
                margin-bottom: 15px;
            }

            &__Season {
                font-size: 20px;
                font-weight: 300;
                padding: 5px;
                margin-bottom: 15px;
                max-width: 120px;
                text-align: center;
            }

            &__Desc {
                font-weight: 400;
                font-size: 17px;

            }

            &__Genres {
                font-size: 20px;
                font-weight: 300;
                margin-top: 15px;
                max-width: 400px;
            }

            &__Cast {
                font-weight: 400;
                font-size: 15px;
                margin-top: 10px;
            }
        }


    }

}